.body{ 
  width: 100vw;
}

.App {
  background: url("./images/sidlo4.png") no-repeat;
  background-size: 100%;
  width: 100%;
}


@media only screen and (max-width: 780px) {
  .App{
    background: url("./images/sidlo4-mobil.png") no-repeat;
    background-size: 100%;
    width: 100%;
    margin-top: -0.5rem;
  }

  .App--mobile{
    background-color: var(--main-color-orange);
    height: 100vh;
    background-image: none;
    margin-top: 0;
  }

  .all-height{
    height: 75vh;
  }
}