.summary-box{
    display: flex;
    flex-direction: column;
    justify-content:center;
}

.title-margin{
    margin-top: 7rem;
    margin-bottom: 1rem;
}

.content-box{
    margin-left: 11.5rem;
    margin-right: 8rem;
    display: grid;
    grid-template-columns: [summary] 40% [formular] 60%;
}

.summary-list{
    margin-top: 1rem;
    display: grid;
    grid-template-columns: [a] 80%  [b] 10%;
    justify-content: center;
    /* background: linear-gradient(90deg, white, #fdf2da); */
    border-radius: 5rem;
    /* border: 0.12rem var(--main-color-brown) solid; */
    
}

.summary-01{
    background: transparent;
    padding: 0;
}   

.summary-02{
    background: transparent;
    padding: 0;
}

.services-item--long{
    height: 3rem;
}

.title-center{
    text-align: center;
    margin-left: 0;
    margin-top: 12rem;
}

.text-center{
    text-align: center;
    margin-bottom: 3.3rem;
}
