.prices{
    padding-top: 7rem; 
    color: var(--main-color-brown)
}

.services-list, .check-list-01, .check-list-02, .check-list-03{
    list-style: none;
    margin: 0;
    padding: 2.5rem 1rem 1rem 1rem;
}

.check-list-03{
    padding-right: 1.5rem;
}

.check-img{
    max-width: 1.7rem;
}

.prices-box{
    display: grid;
    width: 70%;
    margin: auto;
    grid-template-columns: [a] 57% [b] 14% [c] 14% [d] 15%;
    border-radius: 5rem;
}

.services-item, .check-item{
    height: 2.2rem;
    margin-bottom: 1rem;
}

.services-item{
    overflow: auto;
    white-space: nowrap;
}

.services-item--first, .check-item--first, .services-item--last, .check-item--last{
    font-weight: bolder;
    color: var(--main-color-green);
}

.services-item--first{
    text-align: center;
}

.check-list-01, .check-list-02, .check-list-03{
    text-align: center;
}

.services-list{
    text-align: start;
    border-radius: 5rem 0 0 5rem;
    background: linear-gradient(90deg, white, var(--main-color-orange));
}

.services-item--last, .check-item--last{
    margin-bottom: 0;
}

.check-list-01{
    background-color: #fdf2da;
}

.check-list-02{
    background-color: #faebcc;
}

.check-list-03{
    background-color: #f9e4ba;
    border-radius: 0 5rem 5rem 0;
}

.prices-ps{
    width: 70%;
    margin: auto;
    text-align: start;
    padding: 0.2rem;
    font-size: 0.8rem;
}

.assistantPrice-box{
    display: grid;
    grid-template-areas: "a"
                         "a";
    width: 70%;
    margin: auto;
    margin-top: 2rem;
    text-align: center;
    background: linear-gradient(90deg, white, #f9e4ba);
    border-radius: 5rem;
    padding: 3rem 0;
}

.assistantPrice-item{
    height: 2.5rem;
}

.assistanPrice-item--last{
    color: var(--main-color-brown);
}


.check-click{
    color: azure;
    border: none;
    border-radius: 0.5rem;
    background-color: var(--main-color-green);
    padding: 0.4rem;
    width: auto;
    font-size: 1rem;
}

@media only screen and (max-width: 780px) {
    .prices{
        display: none;
    }
}