.footer {
    background-color: var(--main-color-brown);
    color: azure;
    margin-top: 3rem;
    background-image: url('./images/edited-logo1.webp');
    background-repeat: no-repeat;
    background-size: 15%;
    background-position-x: 30%;
    background-position-y: center;
  }
  
.footer-bottom{
  width: 100%;
  position: absolute;
  bottom: 0;

}

  .footer-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 1.5rem;
    text-transform: uppercase;
    text-align: center;
    padding-top: 0.5rem;
    align-items: center;
    margin-left: 40%;
    margin-right: 5%;
  }
  
  .footer-list {
    font-size: 1rem;
    text-transform: none;
    list-style: none;
    text-shadow: none;
    padding: 0;
  }

  .footer-box02{
    text-align: cnter;
    align-self: flex-start;
  }

.links-list{
  color: azure;
  text-align: center;
}
  
  .footer-item {
    padding: 0.4rem 0;
    /* display: flex; */
    justify-content: center;
    height: 1.5rem;
  }

  .footer-item--underline{
    text-decoration: underline;
    cursor: pointer;
  }
  
  .footer-image{
    height: 90%;
    margin: auto 0;  
    margin-right: 0.5rem;
  }
  
  .footer-title{
    color: var(--main-color-orange);
  }
  
  a {
    color: azure;
  }

  @media only screen and (max-width: 780px) {
    .footer{
      display: none;
    };
  }