.ad-box{
    margin: 3rem 0;
    display: flex;
    justify-content: center;
}

.ad-image{
    width: 100%;
    padding: 0.5rem;
    border: 0.1rem var(--main-color-grey) solid;
    border-radius: 0.5rem;
}

.ad-anchor{
    width: 40% ;
    display: flex;
}


.ad-ad{
    color: rgba(0, 0, 0, 0.445);
    margin-left: -1.5rem;
}

@media only screen and (max-width: 780px) {
    .ad-box{
        display: none;
    }
}